table {
  border-collapse: collapse;
}

th,
td {
  width: 100px;
}
th {
  border-bottom: 1px solid black;
}

thead > tr {
  position: relative;
  display: block;
}

tbody {
  display: block;
  height: 150px;
  overflow: auto;
}

tbody::-webkit-scrollbar {
  width: 0 !important;
}
