.small {
  font-size: 14px;
  border-radius: 4px;
  height: 24px;
  padding: 0px 12px;
}
.medium {
  padding: 0px 12px;
  font-size: 14px;
  height: 32px;
  border-radius: 3px;
}
.large {
  padding: 0px 12px;
  font-size: 16px;
  height: 40px;
  border-radius: 3px;
}
.button {
  display: inline-block;
  outline: none;
  color: #fff;
  margin: 4px;
  font-weight: 600;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  border: 1px solid transparent;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.input-icons i {
  position: absolute;
  cursor: pointer;
}

.input-icons {
  width: auto;
  margin-bottom: 10px;
}

.icon {
  padding-left: 8px;
  padding-top: 8px;
  text-align: left;
  width: 24px;
  height: 24px;
}
.icon-small {
  composes: icon;
  padding-top: 4px;
}
.icon-large {
  composes: icon;
  padding-top: 12px;
  padding-left: 4px;
}

.primary {
  composes: button;
  color: #ffffff;
  background-color: #2773ff;
}
.primary:hover {
  background-color: #2368e6;
}
.primary:focus {
  border: solid 1px #749be2;
}
.primaryicon {
  composes: button;
  color: #ffffff;
  text-align: right;
  background-color: #2773ff;
  width: 89px;
}
.primaryicon:hover {
  background-color: #2368e6;
}
.primaryicon:focus {
  border: solid 1px #749be2;
}
.secondary {
  composes: button;
  color: #000000;
  background-color: #ffffff;
  border: solid 1px #ededed;
}
.secondary:hover {
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1);
}
.secondary:focus {
  border: solid 1px #2773ff;
}
.disabled {
  composes: button;
  background-color: #ffffff;
  border: solid 1px #e4e4e4;
  color: #e4e4e4;
  pointer-events: not-allowed;
}
.display-none {
  display: none;
}
