@import url("https://rsms.me/inter/inter.css");
.font-inter {
  font-family: Inter;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.43;
  letter-spacing: normal;
}
.selectContainer {
  display: flex;
  display: inline-block;
}
.selectStyle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 300px;
  height: 32px;
  border-radius: 4px;
  border: solid 1px #333333;
  background-color: #ffffff;
  cursor: pointer;
  user-select: none;
}
.selectStyle_click {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 300px;
  height: 32px;
  border-radius: 4px;
  border: 1px solid #0091ff;
  background-color: #ffffff;
  cursor: pointer;
  user-select: none;
}

.item2 {
  padding-top: 5px;
  padding-right: 5px;
}

.selectedTxt {
  composes: font-inter;
  color: #333333;
  padding: 6px 124px 6px 12px;
}
.selectDropdown {
  user-select: none;
  margin-top: 4px;
  overflow-y: auto;
  width: 300px;
  max-height: 140px;
  border-radius: 4px;
  border: solid 1px #cccccc;
}
.dropdownList {
  composes: font-inter;
  height: 32px;
  display: block;
  padding: 4px 0 0 12px;
}
.dropdownList:hover {
  background-color: #f0f0f0;
  cursor: pointer;
}
.optionStyle {
  background: #f4f5f6;
}
